.time-0 {
  color: #f1c40f; }

.time-1 {
  color: #287fb8; }

.time-0 .clock {
  background: #f1c40f; }

.time-1 .clock {
  background: #287fb8; }

.clock-icon {
  font-size: 2em; }

.info-banner {
  font-size: 0.8em;
  background: white;
  padding: 15px 10px; }

.clock {
  display: block;
  font-size: 5em;
  user-select: none;
  padding: 5% 0; }

button {
  border: 0;
  outline: 0;
  background: transparent; }

.ampm {
  font-weight: bold;
  display: inline-flex;
  border-radius: 9999px;
  background: transparent;
  padding: 6px;
  font-size: 16px !important; }

.ampm:hover {
  transform: translateY(-2px); }

.analog {
  box-shadow: inset 0 -10px 0 0 rgba(255, 255, 255, 0.25);
  padding: 15px 0;
  line-height: 0.85; }

.analog:hover {
  transition: all 400ms ease-out;
  transform: translateY(2px);
  color: white; }

.blinking {
  color: white;
  animation: blinker 1100ms linear infinite; }

@keyframes blinker {
  50% {
    opacity: 0; } }
